
















































































































import Vue from 'vue'
import { getCoursePages, changeState } from '@/services/course'

export default Vue.extend({
  name: 'CourseList',
  data () {
    return {
      filterParams: {
        couseName: '',
        status: '',
        currentPage: 1,
        pageSize: 20
      },
      courses: [],
      totalCount: 0,
      loading: true,
      courseStatusOptions: [{
        value: '',
        label: '全部'
      }, {
        value: '0',
        label: '下架'
      }, {
        value: '1',
        label: '上架'
      }]
    }
  },
  created () {
    this.loadCourses()
  },
  methods: {
    async loadCourses () {
      this.loading = true // 展示加载中状态
      const { data } = await getCoursePages(this.filterParams)
      // eslint-disable-next-line
      data.data.records.forEach((item: any) => {
        item.isStatusLoading = false
      })
      this.courses = data.data.records
      this.totalCount = data.data.total
      this.loading = false // 关闭加载中状态
    },
    onSubmit () {
      this.filterParams.currentPage = 1 // 筛选查询从第 1 页开始
      this.loadCourses()
    },
    handleSizeChange (val: number) {
      this.filterParams.pageSize = val
      this.filterParams.currentPage = 1
      this.loadCourses()
    },
    handleCurrentChange (val: number) {
      // 请求获取对应页码的数据
      this.filterParams.currentPage = val // 修改要查询的页码
      this.loadCourses()
    },
    // eslint-disable-next-line
    async onStateChange (course: any) {
      course.isStatusLoading = true
      try {
        await changeState({
          courseId: course.id,
          status: course.status
        })
        this.$message.success(`${course.status === 0 ? '下架' : '上架'}成功`)
      } catch (err) {
        console.log(err)
      }
      course.isStatusLoading = false
    }
  }
})
